.container {
    background-color: hsl(217, 19%, 24%);
    width: 550px;
    height: 250px;
    border-radius: 15px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 70%);
}

.adviceNumber {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 5px;
    color: hsl(150, 100%, 66%);
}

.loading{
    color: hsl(193, 38%, 86%);
    font-size: 16px;
}

.adviceText{
    margin: 40px 60px;
    font-weight: 900;
    font-size: 18px;
    color: hsl(193, 38%, 86%);
}

.dividerMobile{
    display: none;
}

.btnDice {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: hsl(150, 100%, 66%);
    position: absolute;
    top: 90%;
    left: 45%;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.btnDice:hover{
    box-shadow: 0 0 20px 20px hsla(150, 100%, 66%, 0.2);
}

.dice {
    position: absolute;
    top: 30%;
    left: 30%;
}

@media screen and (max-width: 375px) {
    .container{
        width: 90%;
    }
    .dividerDesktop{
        display: none;
    }
    .dividerMobile{
        display: block;
        position: absolute;
        bottom: 40px;
        left: 20px;
    }
    .btnDice{
        left: 40%;
    }
}